.cover {
    padding: 20px 0px;
    /* background: #4285F4; */
    background: black;
    color: white;
    text-align: center;
}

.circle {
    display:block;
    width:150px;
    height: 150px;
    border-radius: 150px;
    background: #fff;
    position: relative;
    top: -10px;
}

.image {
    display:inline-block;
    width: 99%;
    height: 90%;
    border-radius: 90%;
    position: relative;
    top: -40px;
    border: 5px solid white;
}


.name {
    top: 0%;
    position: relative;
    /* left: 34%; */
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: xx-large;
    text-decoration: underline;
    text-align: center;
    /* background: black;
    padding: 7px 7px; */
}

